import { Component, ElementRef, EventEmitter, Input, OnInit, Output, SimpleChanges, ViewChild } from '@angular/core';
import { VhAlgorithm, VhQuery, VhQueryCafe } from 'ionic-vhframeworks';
import { Subscription } from 'rxjs';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-search-voucher',
  templateUrl: './search-voucher.component.html',
  styleUrls: ['./search-voucher.component.scss']
})
export class SearchVoucherComponent implements OnInit {
  @ViewChild("inputSearchSales") myInputField: ElementRef;
  @Input("searchProductList") searchProductList: boolean = true;
  @Input() barcode: any;
  @Input() id_product_delete: string;
  @Output("closeSearchProductList") closeSearchProductList = new EventEmitter<any>();
  @Output() clearBarcode = new EventEmitter()
  @Input() default_price_type: any;
  observableAllProducts: Subscription
  indexTab: any = 0;
  id_branch: string = this.vhQueryCafe.getDefaultBranch()._id
  public tabs: any = []
  ListVoucher: any[];

  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhQueryCafe: VhQueryCafe,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    private vhQuery: VhQuery,

  ) { }
  public goodList: any[] = [];
  public searchList: any[] = [];
  public typeGoods = 0;
  search: string = '';
  ngOnInit(): void {
    this.getReleaseVoucher();
  }
  getReleaseVoucher() {
     // ly danh sch voucher  pht hnh
     this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQueryCafe.getPaymentCardCodes_byFields( { status:{ $eq: 1 }},{},{},0),
        // this.vhQueryCafe.getPaymentCards()
      ]).then(([payment_card_codes,]:any)=>{
            let promise = new Array();
            for (let i = 0; i < payment_card_codes.length; i++){
              this.vhQueryCafe.getPaymentCard_Code_Name_Release(payment_card_codes[i]._id)
              .then((payment_card_release_details:any)=>{
                console.log(payment_card_release_details);
                
                // let itemPayment_cards = payment_cards.find(item => item._id == payment_card_release_details.id_payment_card )
                if(payment_card_release_details){
                  payment_card_codes[i]['name']= payment_card_release_details.name_payment_card;
                  payment_card_codes[i]['name_payment_card_release']= payment_card_release_details.name_payment_card_release;
                  payment_card_codes[i]['products']= payment_card_release_details.products.map(e =>{
                    let product = this.vhQueryCafe.getlocalProduct( e.id_product)
                    return {...e , name : product.name}
                  });
                  payment_card_codes[i]['price']= payment_card_release_details.units[0].price;
                  payment_card_codes[i]['type'] = payment_card_release_details.type;
                  payment_card_codes[i]['id_payment_card'] = payment_card_release_details.id_payment_card;
                  this.ListVoucher = payment_card_codes;
                  this.searchList = this.ListVoucher;
                }
              })
            }
            this.vhQuery.asyncPromiseAll(promise)
            .then((array)=>{
              this.vhComponent.hideLoading(0)
            },error=>{
              console.log(error);
              this.vhComponent.hideLoading(0)
            })
      })
    })
  }




  ngOnChanges({ barcode, id_product_delete }: SimpleChanges): void {


    if (id_product_delete && id_product_delete.currentValue) {
      this.searchList.find(item => item._id == id_product_delete.currentValue).choose = false
    }
    if (barcode)
      if (barcode.currentValue) {
        if (barcode.currentValue.toString().length > 5) {
          let product = this.searchList.filter(item => item.barcode == barcode.currentValue);
          if (product[0]) { this.chooseProduct(product[0]); this.clearBarcode.emit(true) }
          else {
            this.vhComponent.alertMessageDesktop('error', this.languageService.translate("Not found"))
            this.clearBarcode.emit(true)
          }
        }
      }
  }

  ngOnDestroy() {
    if (this.observableAllProducts) this.observableAllProducts.unsubscribe();
  }
  ngAfterViewInit() {
    this.myInputField.nativeElement.focus();
  }
  acticeFocus() {
    this.myInputField.nativeElement.focus();
  }
  goBack() {
    this.closeSearchProductList.emit(false);
  }

  tempVal: string = ''
  searchProduct(value: string) {
    this.tempVal = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length)
      this.searchList = this.vhAlgorithm.searchList(this.tempVal, this.ListVoucher, ['code'])
    else this.searchList = this.ListVoucher
  }

  chooseProduct(item) {
    item.choose = true;
    let dataConfig = {
      _id: item._id,
      name: item.name,
      id_payment_card: item.id_payment_card,
      id_payment_card_code: item._id,
      name_payment_card_release : item.name_payment_card_release,
      code: item.code,
      quantity: 1,
      pctype: 1,
      price: item.price,
      products: item.products,
    }

    this.closeSearchProductList.emit(dataConfig)
  }

}
