
import { NzModalService } from 'ng-zorro-antd/modal';
import { Component, OnInit, HostListener, ChangeDetectorRef } from '@angular/core';
import { ActivatedRouteSnapshot, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuery, VhQueryCafe } from 'ionic-vhframeworks';
import { Observable } from 'rxjs';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { FunctionService } from 'src/app/cafe/services/function.service';
import { SalesPaymentCardA4Component } from 'src/app/cafe/components/print/sales-payment-card/sales-payment-card-a4/sales-payment-card-a4.component';
import { SalesPaymentCardA5Component } from 'src/app/cafe/components/print/sales-payment-card/sales-payment-card-a5/sales-payment-card-a5.component';
import { SalesPaymentCardK80Col4Component } from 'src/app/cafe/components/print/sales-payment-card/sales-payment-card-k80-col4/sales-payment-card-k80-col4.component';
import { SalesPaymentCardK80Col3Component } from 'src/app/cafe/components/print/sales-payment-card/sales-payment-card-k80-col3/sales-payment-card-k80-col3.component';
import { SalesPaymentCardK57Col3Component } from 'src/app/cafe/components/print/sales-payment-card/sales-payment-card-k57-col3/sales-payment-card-k57-col3.component';

import { VhBillType50Add, VhBillType50DetailAdd } from '../bill-type50-interface';

@Component({
  selector: 'bill-type50-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

  public root: any; // bin nhn state truyn qua
  public invoice: VhBillType50Add = {
    payment_type: 1,
    date: new Date(),
    id_customer: 'id_retail',
    id_branch: '',
    id_employee: '',
    id_wallet: 'id_cash',
    total: 0,
    payment: 0,
    tax: 0,
    discount: 0,
    fee: 0,
    bill_type: 50,
    note: '',
    cash: 0,
    bill_code: ''
  }

  public listTax: any[] = this.vhQueryCafe.getlocalTaxs().filter(tax => tax.status);
  invoice_detail: VhBillType50DetailAdd[] = []
  subTotal = 0;
  taxValue = 0
  public employee: any = {};
  public pickerTime = new Date();
  public customer: any;
  public checkPrinter: boolean = false;
  public closerBrower: any;
  public id_product_delete: string = '';
  public percent_discount_bill: number = 0;

  constructor(
    private router: Router,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    public vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    public vhAuth: VhAuth,
    private nzModalService: NzModalService,
    private fncService: FunctionService,
    private cdf: ChangeDetectorRef,
    private vhQuery: VhQuery
  ) {
    this.root = this.router.getCurrentNavigation().extras.state;
  }

  ngOnInit(): void {

    // set nhn vin mc nh 
    this.invoice.id_employee = (this.vhAuth.getUser()._id)
    this.employee = this.vhAuth.getlocalEmployee(this.invoice.id_employee);
    this.invoice.id_branch = (this.vhQueryCafe.getDefaultBranch()._id)

    this.customer = this.vhQueryCafe.getlocalCustomer(this.invoice.id_customer);

  }
  event: any // bin lng nghe
  ngOnDestroy() {
    document.removeEventListener('keydown', this.event, false);
    // window.removeEventListener('beforeunload', this.closerBrower, false)
  }
  /**
   * lng nghe sk nhn f1 f2 f3 f4 trn bn phm
   * @example 
   */
  eventKeypress() {
    this.event = (event: any) => {
      const keycode = event.keyCode;
      if (keycode === 112 || keycode === 113 || keycode === 114) event.preventDefault();
      if (keycode === 115) this.searchProductList = !this.searchProductList;
      else if (this.invoice_detail.length > 0) {
        switch (keycode) {

          case 113: {
            if (this.checkPrinter) this.payInvoice(50, true)
            break;
          }
        }
      }
    }

    document.addEventListener('keydown', this.event, false);
  }
  ngAfterViewInit() {
    let tax = this.listTax.find(item => item.default);
    if (tax) {
      this.id_tax = tax._id
      this.invoice.tax = (tax['value']);
    }
    let printer = this.vhQueryCafe.getLocalPrintPage('page_desktop_sales_payment_card')
    if (printer) this.checkPrinter = printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable;
    this.vhAlgorithm.waitingStack().then(() => {
      this.discount_price = document.querySelector(".discount-price") ? this.vhAlgorithm.vhnumeral(".discount-price") : 0;
      this.invoice_fee = document.querySelector(".invoice-fee") ? this.vhAlgorithm.vhnumeral(".invoice-fee") : 0
    })

    // ------bt s kin nhn phm nng-----
    this.eventKeypress();
  }

  ngAfterViewChecked(): void {
    this.cdf.detectChanges()
  }


  // edit gi th
  editCardPrice(nameClass, position) {
    let price = this.vhAlgorithm.vhnumeral("#" + nameClass);
    this.invoice_detail[position].price = parseFloat(price.getRawValue() ? price.getRawValue() : 0)
    this.setSubTotal();
  }


  /**
   * bt sk nhn nt xa th
   * @param detail 
   * @param index 
   */
  deleteGoods(detail: VhBillType50DetailAdd, index?) {
    if (detail) {
      this.id_product_delete = detail.id_payment_card_code
      this.removeInvoiceDetail(detail.id_payment_card_code)

    }
  }

  // -------change tax------
  id_tax: string;
  /**
   * bt sk thay i thu ca bill
   * @param event 
   */
  changeTax(event) {
    let tax = this.listTax.find(item => item._id == event);
    this.invoice.tax = (tax.value);
    this.setTaxValue();
  }

  searchProductList: boolean = false;
  /**
   * m component chn sp
   * @example this.openSearchProductList()
   */
  openSearchProductList() {
    this.searchProductList = true;
  }

  // to danh sch voucher bn
  closeSearchProductList(data) {
    if (typeof data === 'boolean') {
      this.searchProductList = false;
    } else {
      if (data) {
        if (!this.invoice_detail.includes(data)) {
          this.addInvoiceDetail(data)
          this.vhComponent.alertMessageDesktop("success", this.languageService.translate("Thm voucher thnh cng"))
        } else {
          this.vhComponent.alertMessageDesktop("error", this.languageService.translate("M voucher  c chn !"))
        }
      }
    }

  }

  goBack() {
    this.router.navigate(['/cafe/sales-payment-card']);
  }

  // -------modal edit unit price-------
  isVisibleEditUnitPrice: boolean = false;
  unit_price: any;
  tempDetail: VhBillType50DetailAdd;
  /**
  * m modal thay i gi bn sn phm trong bill
  * @param detail 
  * @example this.openEditUnitPrice(bilL_detail_selected)
  */
  openEditUnitPrice(detail: VhBillType50DetailAdd) {
    this.tempDetail = detail;
    this.isVisibleEditUnitPrice = true;
    this.vhAlgorithm.waitingStack().then(() => {
      this.unit_price = this.vhAlgorithm.vhnumeral('.unit_price');
    });
  }
  /**
   * bt sk nhn nt Ok modal gim gi sp
   * @example this.handleEditUnit()
   */
  handleEditUnit() {
    if (this.unit_price.getRawValue() != '' && this.unit_price.getRawValue() >= 0) {
      this.tempDetail.price = parseFloat(this.unit_price.getRawValue());
      this.setSubTotal();

      this.isVisibleEditUnitPrice = false;
    } else { this.vhComponent.showToast(5000, this.languageService.translate("Invalid data, please check again"), "alert-toast") }
  }
  /**
   * tt modal gim gi sp
   * @example this.handleCancelEditUnit()
   */
  handleCancelEditUnit() { this.isVisibleEditUnitPrice = false }
  // -------set employee-------
  /**
   * bt sk thay i nv bn hng
   * @param event id_employee selected
   */
  closeAndReceiveEmployee(event) {
    if (event) {
      this.invoice.id_employee = (event);
    }
  }
  // ----set customer-----
  radioValue: 1 | 2 = 1;
  /**
  * bt sk thay i khch hng
  * @param event id_customer selected
  */
  closeAndReceiveCustomer(event) {
    if (event) {
      this.invoice.id_customer = (event);
      if (!this.vhQueryCafe.getlocalCustomer(event).debt_enable) this.invoice.payment = (this.getTotalATax());
    }
    this.customer = this.vhQueryCafe.getlocalCustomer(this.invoice.id_customer);
  }
  /**
   * bt sk thay i loi khch hng (member or retail)
   * @param event 
   * @example this.changeCustomerType(1)
   */
  changeCustomerType(event) {
    if (this.radioValue == 1) {
      this.invoice.id_customer = ("id_retail");
      this.invoice.payment = (this.getTotalATax());
    } if (event == 2) {
      this.clearRetailCustomer()
      this.radioValue = 2
    }
  }

  /**
   * clear d liu khch l trong bill trng hp thay i thnh khch thnh vin
   * @example this.clearRetailCustomer()
   */
  clearRetailCustomer() {
    this.invoice.retail_address = ('')
    this.invoice.retail_name = ('')
    this.invoice.retail_phone = ('')
  }

  /**
   * bt sk thay i phng thc thanh ton
   * @param event 
   * @example this.closeAndGetValuePayment(event)
   */
  closeAndGetValuePayment(event) {
    if (event[0] == 1 || event[0] == 2) {
      this.setPaymentType(event[0]);
    }
    else if (event[0] == 3) {
      this.setPaymentType(event[0], event[1]);
    }
  }
  /**
   * cp nht payment_type cho bill
   * @param type 
   * @param id_wallet 
   * @example  this.setPaymentType(1)
   */
  setPaymentType(type: 1 | 2 | 3, id_wallet?) {
    this.invoice.payment_type = (type)
    switch (type) {
      case 1: {
        this.invoice.id_wallet = ('id_cash');
        this.invoice.payment = (this.invoice.payment ? this.invoice.payment : this.invoice.total + this.taxValue);

        break;
      }
      case 2: {
        this.invoice.payment = (0); this.invoice.id_wallet = (null);
        this.invoice.cash = (0);

        break;
      }
      case 3: {
        if (id_wallet) {
          this.invoice.id_wallet = (id_wallet)
          this.invoice.payment = (this.invoice.payment ? this.invoice.payment : this.invoice.total + this.taxValue);

          break;
        }
      }
    };
  }
  /**
  * bt sk thay i date ca ha n , gn li gi tr date mi cho n
  * @example this.setPickerTime('2023-06-12T08:02:27.697Z')
  */
  setPickerTime(event) {
    if (new Date(event).getTime() < new Date(this.vhAuth.getStore().registrationdate).getTime()) {
      this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the registration date (")} ${this.fncService.formatDate(this.vhAuth.getStore().registrationdate)} ${")"}`, 5000)
      this.pickerTime = new Date(this.invoice.date)
      this.invoice.date = (new Date(this.pickerTime));
    }
    else this.vhQueryCafe.getValidityDate(new Date(event)).then((validitydate: any) => {
      if (validitydate == null) {
        this.invoice.date = (event);
      }
      else {
        this.pickerTime = new Date(validitydate)
        this.invoice.date = (new Date(this.pickerTime));
        this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 5000)
      }
    })
  }

  discount_price: any;
  /**
   * bt sk thay i gim gi ca ha n , gn li gi tr gim gi mi cho n
   * @example this.editDiscount()
   */
  editDiscount() {
    if (this.discount_price.getRawValue() && this.discount_price.getRawValue() >= 0 && this.discount_price.getRawValue() <= this.subTotal) {
      this.invoice.discount = (parseFloat(this.discount_price.getRawValue()));
      this.percent_discount_bill = this.subTotal ? (this.invoice.discount / this.subTotal) * 100 : 0
    } else {
      document.getElementById("discount-price")['value'] = this.invoice.discount;
      this.discount_price = this.vhAlgorithm.vhnumeral('.discount-price');
    }
    this.setTotal()
  }

  // -------change tax------

  invoice_fee: any;
  /**
   * bt sk thay i ph ca ha n , gn li gi tr ph mi cho n
   * @example this.editFee()
   */
  editFee() {
    if (this.invoice_fee.getRawValue() && this.invoice_fee.getRawValue() >= 0) {
      this.invoice.fee = (parseFloat(this.invoice_fee.getRawValue()));
    } else {
      document.getElementById("invoice-fee")['value'] = this.invoice.fee;
      this.invoice_fee = this.vhAlgorithm.vhnumeral('.invoice-fee');
    }
    this.setTotal()
  }

  // -------modal edit pay---------
  edit_pay: boolean = false;
  edit_paytotal: any;
  /**
  * m modal chnh sa thanh ton ca bill
  * @example this.showModalEditTotalPay()
  */
  showModalEditTotalPay() {
    if (this.invoice.payment_type != 2 && this.vhQueryCafe.getlocalCustomer(this.invoice.id_customer).debt_enable) {
      this.vhAlgorithm.waitingStack().then(() => {
        this.edit_paytotal = this.vhAlgorithm.vhnumeral(".edit-pay");
      })
      this.edit_pay = true;
    }
    else if (!this.vhQueryCafe.getlocalCustomer(this.invoice.id_customer).debt_enable) {
      this.vhComponent.showToast(5000,
        this.languageService.translate("This customer is not allowed to debt"), "alert-toast"
      )
    }
  }
  /**
   * bt sk nhn nt ok modal chnh sa thanh ton ca bill
   * @example this.handleEditPay()
   */
  handleEditPay() {
    if (this.edit_paytotal.getRawValue() >= 0 && this.edit_paytotal.getRawValue() <= this.getTotalATax()) {
      this.invoice.payment = (parseFloat(this.edit_paytotal.getRawValue()))
    }
    this.edit_pay = false;
  }
  /**
   * bt sk nhn nt hy modal chnh sa thanh ton ca bill
   * @example this.handleCancelEditPay()
   */
  handleCancelEditPay() {
    this.edit_pay = false;
  }
  /**
   * bt sk thay i tin khch a
   * @param cash 
   */
  updateCash(cash: any) {
    this.invoice.cash = parseFloat(cash.replaceAll(',', ''))
  }

  /**
   * bt sk thay i gim gi bill theo %
   * @param value % gim
   * @example this.editPercentDiscount(10) 
   */
  editPercentDiscount(value) {
    if (parseFloat(value) > 100)
      document.getElementById("percent-discount-price")['value'] = 0
    else this.percent_discount_bill = value;
    this.invoice.discount = this.subTotal * (this.percent_discount_bill / 100)
    this.setTotal()
  }

  save_enable = true;
  /**
     * x l sk nhn nt thanh ton hoc lu tm
     * @param bill_type 
     * @param print bin c  check in hay ko
     * @example this.payInvoice(1)
     */
  payInvoice(bill_type: 50, print?) {
    if (this.vhAuth.checkMyPermission("sales_enable_payment")) {
      if (print) {
        if (this.checkPrinter) {
          if (this.save_enable) {
            this.save_enable = false;
            this.vhComponent.showLoading("", "transparent-loading").then(() => {
              this.createOrUpdate(bill_type).then(bill => {
                this.save_enable = true;
                if (bill.bill_code) this.invoice.bill_code = (bill.bill_code)
                this.vhComponent.hideLoading(0).then(() => {
                  this.vhComponent.showToast(5000,
                    `${this.languageService.translate("Sales invoice")} ${bill.bill_code || this.invoice.bill_code} ${this.languageService.translate("has been created successfully")}`, "success-toast"
                  )
                  this.handlePrint()
                })
              })
            })
          }
        }
        else {
          this.nzModalService.confirm({
            nzTitle: '<i>' + this.languageService.translate("Function") + '</i>',
            nzContent: '<b>' + this.languageService.translate("Please open the function in") + ' ' + '<i>' + '"' + this.languageService.translate("Sales page settings") + '"' + '</i>' + ' ' + ' -> ' + '<i>' + '"' + this.languageService.translate('Print page') + '"' + '</i>' + '</b>',
            nzIconType: "info-circle",
            nzCancelText: null,
            nzOnOk: () => { }
          })
        }
      }
      else {
        if (this.save_enable) {
          this.save_enable = false;
          this.vhComponent.showLoading("", "transparent-loading").then(() => {
            this.createOrUpdate(bill_type).then(bill => {
              this.save_enable = true;

              if (bill.bill_code) this.invoice.bill_code = (bill.bill_code)
              this.vhComponent.hideLoading(0).then(() => {
                this.vhComponent.showToast(5000,
                  `${this.languageService.translate("Sales invoice")} ${bill.bill_code || this.invoice.bill_code} ${this.languageService.translate("has been created successfully")}`, "success-toast"
                )
                this.goBack()
              })
            })
          })
        }
      }

    }
    else this.vhComponent.showToast(5000, this.languageService.translate('You do not have this rights'), "alert-toast")
  }

  /**
   * to bill ln DB
   * @param bill_type 
   * @returns 
   */
  createOrUpdate(bill_type: 50) {
    let invoice = { ...this.invoice }
    return this.vhQueryCafe.createBill_Billdetail(invoice, this.getCreateUpdateInvoiceDetail())

  }
  /**
  * ly thng tin d liu trang in
  * @returns Promise<any>
  * @example this.checkPrint()
  */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQueryCafe.getlocalPrinters().find(item => item.desktop)
      if (printer && printer.prepaid_card.enable) resolve(printer.prepaid_card)
      else resolve(false)
      if (!printer) rejects(false)
    })
  }

  /**
   * tr v component ca trang in tng ng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return SalesPaymentCardK57Col3Component
      case 'print_size_k80_3c': return SalesPaymentCardK80Col3Component
      case 'print_size_k80_4c': return SalesPaymentCardK80Col4Component
      case 'print_size_a5': return SalesPaymentCardA5Component
      case 'print_size_a4': return SalesPaymentCardA4Component
      default: return SalesPaymentCardA4Component
    }
  }


  /**
   * kim tra d liu thng tin my in v m component in
   * @example this.handlePrint()
   */
  handlePrint() {
    let print = this.vhQueryCafe.getLocalPrintPage('page_desktop_sales_payment_card')
    if (print && print.print_sizes[print.print_sizes.findIndex(e => e._id == print.print_size_default)].enable) {
      let printer = print.print_sizes[print.print_sizes.findIndex(e => e._id == print.print_size_default)]
      if (printer) {
        let type = printer._id;
        this.vhComponent.showModal(this.renderPrintPage(type), { printer: printer, invoice: this.invoice, invoice_detail: this.invoice_detail }, false, false, `modal-print-${type}`).then((modal) => {
          modal.onWillDismiss().then(() => this.goBack())
        })
      }
    }
  }
  note: string;
  /**
  * bt sk thay i note ca bill
  * @example this.edit_note()
  */
  edit_note() {
    if (this.note) {
      this.invoice.note = (this.note)
    }
  }

  //--------------visible drawer info customer-----------
  visibleInfoCustomer: boolean = false;
  /**
  * m modal xem thng tin khch hng
  * @example this.openInfoCustomer()
  */
  openInfoCustomer() {
    if (this.invoice.id_customer != 'id_retail') {
      this.customer = this.vhQueryCafe.getlocalCustomer(this.invoice.id_customer);
      this.visibleInfoCustomer = true;
    }
  }
  /**
   * ng modal xem thng tin khch hng
   * @example this.openInfoCustomer()
   */
  closeInfoCustomer(event) {
    this.visibleInfoCustomer = false;
  }

  // ------thot trang-----
  isExit: boolean = false;
  checkDeactivate(currentRoute: ActivatedRouteSnapshot, currentState: RouterStateSnapshot, nextState?: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    return nextState.url == '/login' ? true : (this.isExit ? this.isExit : this.vhComponent.alertConfirm(this.languageService.translate("Confirm"), "", this.languageService.translate("Are you sure to cancel the sales order"), "OK", (this.languageService.translate("Cancel"))).then(() => { return true }).catch(() => { return false }))
  }


  /**add customer */
  showDrawerAddCusomer: boolean = false;
  showDrawerAddRetail: boolean = false;
  /**
   * m modal thm khch hng thnh vin mi hoc thm thng tin khch l
   * @example this.openDrawerAddCustomerOrRetail()
   */
  openDrawerAddCustomerOrRetail() {
    if (this.radioValue == 1) this.showDrawerAddRetail = true
    else this.showDrawerAddCusomer = true
  }
  /**
   * bt sk tt drawer thm khch hng
   * @param result output kt qu tr v
   * @example this.closeAddCustomer('NDWOIUHDIUWQHSIOQUH')
   */
  closeAddCustomer(result) {
    if (this.radioValue == 1) {
      if (result) {
        this.invoice.retail_name = (result.retail_name)
        this.invoice.retail_phone = (result.retail_phone)
        this.invoice.retail_address = (result.retail_address)
      }
      this.showDrawerAddRetail = false
    }
    else this.showDrawerAddCusomer = false
  }

  barcode: string = ''
  barcode_array = [];
  @HostListener("window: keypress", ["$event"])
  /**
   * S kin nhn barcode
   */
  getKeyUp(_event: KeyboardEvent) {
    if (this.router.url === "/sales-payment-card/create") {
      // if (!this.showSearchBar && !this.showPayInfo) {
      if (_event.key == "Enter") {
        this.barcode_array.forEach((i, index) => {
          if (i == "Shift") {
            this.barcode_array[index + 1] = this.barcode_array[
              index + 1
            ].toUpperCase();
            this.barcode_array.splice(index, 1);
          }
        });
        // this.scanBarcode(this.barcode_array.join(""));
        this.barcode = this.barcode_array.join("");
        setTimeout(() => {
          this.barcode_array = [];
        }, 100);

      } else {
        this.barcode_array.push(_event.key);
      }
      // }
    }
  }

  /**
   * tnh li cng tin ca bill khi c s thay i (thm xa sa bill_detail, i gi bn, khuyn mi, ph ,thu ,...)
   * @example this.setSubTotal()
   */
  setSubTotal() {
    this.subTotal = this.invoice_detail.reduce((prev: number, next) => prev + 1 * next.price, 0)
    this.setTotal();
  }

  /**
   * tnh li tng tin ca bill  lu trng total
   * @example this.setTotal();
   */
  setTotal() {
    this.invoice.total = this.subTotal - this.invoice.discount + this.invoice.fee
    this.setTaxValue()
  }

  /**
   * tnh gi tr thu ra tin
   * @example this.setTaxValue()
   */
  setTaxValue() {
    this.taxValue = this.invoice.total * (this.invoice.tax / 100)
    this.setPayment()
  }

  /**
   * tnh li thanh ton  lu trng payment ca bill
   * @param value : gi tr thanh ton t nhp nu c (trng hp n li 1 phn)
   * @example this.setPayment()
   */
  setPayment(value?: number) {

    if (this.invoice.payment_type == 2) {
      this.invoice.payment = 0
    } else {
      if (value != null)
        this.invoice.payment = value
      else
        this.invoice.payment = this.invoice.total + this.taxValue
    }
    this.setCash(this.invoice.cash);
  }
  /**
   * cp nht li tin khch a  lu trng cash trong bill
   * @param value 
   * @example this.setCash(100000)
   */
  setCash(value) {
    if (this.invoice.payment_type == 2) this.invoice.cash = 0
    else {
      if (value) this.invoice.cash = parseFloat(value);
      else this.invoice.cash = 0
    }
  }
  /**
   * tr v tng tin 
   * @returns number
   * @example let total = this.getTotalATax()
   */
  getTotalATax(): number {
    return this.invoice.total + this.taxValue
  }
  /**
   * thm sn phm c chn t drawer search-product
   * @param value 
   * @example this.addInvoiceDetail(product_selected)
   */
  addInvoiceDetail(value: any) {
    let detail: any = {
      id_payment_card_code: value.id_payment_card_code,
      name: value.name,
      id_payment_card: value.id_payment_card,
      code: value.code,
      id_bill: value.id_bill,
      pctype: 1,
      price: value.price,
      products: value.products,
      name_voucher_release: value.name_voucher_release
    }
    this.invoice_detail.push(detail)
    this.setSubTotal()
  }
  /**
   * xa 1 bill_detail trong local
   * @param id id_bill_detail selected
   * @param notSetSub 
   */
  removeInvoiceDetail(id: string, notSetSub?: boolean) {
    let index = this.invoice_detail.findIndex(detail => detail.id_payment_card_code == id)
    if (index != -1) {
      this.invoice_detail.splice(index, 1);
    }
    this.setSubTotal()
  }
  /**
   * tr v d liu thng tin bill_detail clean  lu DB
   * @returns array[object]
   *  @example let bill_details = this.getCreateUpdateInvoiceDetail()
   */
  getCreateUpdateInvoiceDetail(): VhBillType50DetailAdd[] {
    return this.invoice_detail.map(item => {
      let detail: VhBillType50DetailAdd = {
        id_payment_card_code: item.id_payment_card_code,
        id_payment_card: item.id_payment_card,
        code: item.code,
        pctype: 1,
        ratio: 1,
        quantity: 1,
        price: item.price,
        products: item.products.map(pro => {
          let data = {
            id_product: pro.id_product,
            ptype: pro.ptype,
            ratio: pro.ratio,
            quantity: pro.quantity,
            price: 0,
          }
          if (pro.id_subproduct) data['id_subproduct'] = pro.id_subproduct
          if (pro.combos) {
            data['combos'] = pro.combos.map(combo => {
              let product = {
                id_product: combo.id_product,
                price: combo.price,
                quantity: combo.quantity,
                ptype: combo.ptype,
                ratio: combo.ratio ? combo.ratio : 1,
              }
              if (combo.id_lotproduct) product['id_lotproduct'] = combo.id_lotproduct
              if (combo.id_subproduct) product['id_subproduct'] = combo.id_subproduct
              return product;
            })
          }
          return data
        }),
      }

      return detail
    })
  }
}
